.audio-player {
  display: flex;
  position: fixed;
  bottom: 1%;
  right: 1%;
  width: 180px;
  height: 180px;
  border-radius: 30px;
  justify-content: center;
  align-items: last baseline;
  z-index: 2;
  overflow: hidden;
  border: 1px solid #00000080;
}

.audio-player-vinil {
  position: absolute;
  width: 80%;
  height: 80%;
  border-radius: 50%;
  background-color: #282727;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  transition: background-color 0.3s ease;
  z-index: 1;
}

.audio-player-vinil-pin {
  position: absolute;
  width: 50%;
  height: 10%;
  margin: -150px auto auto -60%;
  border-top: 5px solid #FFF;
  border-left: 15px solid #FFF;
  border-radius: 60px 0 0 0;
  transform: rotate(-90deg);
  z-index: 5;
}

.audio-player-vinil-content {
  display: flex;
  flex-direction: row;
  position: absolute;
  justify-content: space-around;
  z-index: 3;
}

.icon-btn:hover {
  filter: brightness(85%);
  cursor: pointer;
}

.audio-player-center {
  width: 40%;
  height: 40%;
  border-radius: 50%;
  background: radial-gradient(
    circle at center,
    #7b806e 0%,
    #787e69 40%,
    #79806a 100%
  );
  box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.05),
    0 0 6px rgba(255, 255, 255, 0.3);
  position: relative;
  overflow: hidden;
}

.audio-player-center::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 10%;
  height: 10%;
  border-radius: 50%;
  background: radial-gradient(circle, #bfaea0 0%, #a89d90 70%, #9a8f83 100%);
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2), 0 0 2px rgba(255, 255, 255, 0.3);
}

.volume-slider-container {
  position: absolute;
  top: -15px;
  width: 70%;
  left: 40%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2;
  transform: rotate(180deg);
}

.volume-slider {
  appearance: none;
  width: 100%;
  height: 4px;
  background: #ffffff;
  border-radius: 5px;
  outline: none;
  transition: background 0.3s ease;
}

.audio-player-vinyl-rings {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: transform 0.3s linear;
}

@keyframes spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.audio-player-vinyl-rings.spinning {
  animation: spin 3s linear infinite;
}

@media (max-width: 768px) {
  .audio-player {
    width: 120px;
    height: 120px;
    opacity: 1;
    background: none;
  }

  .audio-player-vinil-pin {
    margin-top: -100px;
    /* display: none; */
  }

  .volume-slider {
    /* display: none; */
    margin-right: -20px;
  }

  .icon-btn:hover {
    filter: brightness(85%);
    cursor: pointer;
    opacity: 1;
  }
}